.product-image-container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.left {
  display: flex;
  height: 100%;
  gap: 1rem;
}

.left_1 {
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 1;
}

.img_wrap {
  width: 80px;
  height: 80px;
  border: 1px solid #eee;
  cursor: pointer;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.img_wrap img {
  width: 70px;
  height: 70px;
  object-fit: contain;
}

.left_2 {
  flex: 1;
  position: relative;
}

.left_2 img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Style for react-image-magnify container */
.left_2 > div {
  z-index: 1000 !important;
}

/* Style for the enlarged image container */
.rmImgBox {
  z-index: 1000 !important;
}

/* Ensure the magnifier lens appears above other elements */
[class*="magnifier"] {
  z-index: 1000 !important;
}

/* Ensure the enlarged image appears above all other elements */
[class*="enlarged"] {
  z-index: 1000 !important;
}

.active {
  border: 2px solid #e77600;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .left {
    flex-direction: column-reverse;
  }

  .left_1 {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }

  .img_wrap {
    width: 60px;
    height: 60px;
  }

  .img_wrap img {
    width: 50px;
    height: 50px;
  }
}
